import classNames from 'classnames';
import {computed, defineComponent, PropType, provide, ref, Ref} from 'vue';
import {ValidateRules} from '../../../../../types';

export default defineComponent({
  name: 'DForm',
  inheritAttrs: false,
  props: {
    /**
     * cancelokcancelok
     */
    okCancel: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    size: {
      type: String as PropType<'small' | 'large' | 'default'>,
      default: 'default'
    },
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    readOnly: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    labelCol: {
      type: [Number, Object] as PropType<number | { [key: string]: unknown }>
    },
    okText: {
      type: String as PropType<string>,
      default: ''
    },
    cancelText: {
      type: String as PropType<string>,
      default: ''
    },
    inline: {
      type: Boolean as PropType<boolean>
    },
    /**
     * 
     */
    labelWidth: {
      type: [String, Number] as PropType<string | number>
    },
    labelPosition: {
      type: String as PropType<'left' | 'right'>
    },
    hideRequiredMark: {
      type: Boolean as PropType<boolean>
    },
    layout: {
      type: String as PropType<'horizontal' | 'inline' | 'vertical'>,
      default: 'horizontal'
    },
    model: {
      type: Object as PropType<any>
    },
    rules: {
      type: Object as PropType<ValidateRules>
    },
    onSubmit: {
      type: Function as PropType<any>
    },
    wrapperCol: {
      type: [Number, Object] as PropType<number | any>
    }
  },
emits: ['ok', 'cancel'],
  setup(props: any, {emit}) {
    const prefixCls = ref('ant-form');
    const fields: Ref<any[]> = ref([]);
    const clearValidate = (props = []) => {
      const localFields = props.length
        ? (typeof props === 'string'
            ? fields.value.filter(field => props === (field as any).prop)
            : fields.value.filter(field => props.indexOf((field as any).prop) > -1)
        ) : fields.value;
      localFields.forEach(field => {
        (field as any).clearValidate();
      });
    };
    const validate = (callback) => {
      if (!props.model) {
        return;
      }
      let promise;
      let copyCallback = callback;
      // if no callback, return promise
      if (typeof copyCallback !== 'function' && Promise) {
        promise = new Promise((resolve, reject) => {
          copyCallback = valid => {
            const errorField = fields.value.find(it => it.currentValidateStatus === 'error');
            if (errorField) {
              errorField.focus();
            }
            valid ? resolve(valid) : reject(valid);
          };
        });
      }

      let valid = true;
      let count = 0;
      // fieldscallback
      if (fields.value.length === 0 && copyCallback) {
        copyCallback(true);
      }
      let invalidFields = {};
      fields.value.forEach(field => {
        field.validate('', (message, field) => {
          if (message) {
            valid = false;
          }
          invalidFields = Object.assign({}, invalidFields, field);
          if (typeof copyCallback === 'function' && ++count === fields.value.length) {
            copyCallback(valid, invalidFields);
          }
        });
      });

      if (promise) {
        return promise;
      }
    };
    const getLayout = () => {
      if (props.inline) {
        return 'inline';
      } else {
        return props.layout;
      }
    };
    const renderButtons = () => {
      if (props.okCancel) {
        return <div class={prefixCls.value + '-footer-btns'}>
          {
            <a-button onClick={(e) => {
              emit('cancel', e);
            }}>{props.cancelText}</a-button>
          }
          {
            <a-button
              onClick={(e) => {
                emit('ok', e);
              }}
              type={'primary'}
              style={{marginLeft: '8px'}}>{props.okText}</a-button>
          }
        </div>;
      }
    };
    const form = computed(() => ({
      ...props,
      emit,
      addField: (field) => {
        if (field) {
          fields.value.push(field);
        }
      },
      removeField: (field) => {
        if (field.prop) {
          fields.value.splice(fields.value.indexOf(field), 1);
        }
      }
    }));
    provide('form', form);
    return {
      prefixCls,
      fields,
      clearValidate,
      validate,
      getLayout,
      renderButtons
    };
  },
  render(ctx) {
    const {
      prefixCls,
      hideRequiredMark,
      onSubmit,
      $slots
    } = ctx;
    const layout = this.getLayout();
    const formClassName = classNames({
      [`${prefixCls}-hide-required-mark`]: hideRequiredMark
    });
    return (
      <a-form onSubmit={onSubmit}
              layout={layout}
              class={formClassName}>
        {$slots.default?.()}
        {this.renderButtons()}
      </a-form>
    );
  }
});
